.App {
    min-height: 100vh;
    background: rgb(78, 78, 78);
}

.search-form {
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(37, 37, 37);
}

.search-bar {
    width: 50%;
    border: none;
    padding: 10px;
}

.search-button {
    background: rgb(86, 165, 55);
    border: none;
    padding: 10px 20px;
    color: white;
}

.recipes {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}